<template>
  <div class="advertisement-wrapper" ref="wrapper">
    <div
      v-for="(adv, index) in advertisements"
      :key="index"
      :class="{ 'fixed-advertisement': shouldFixAd(index) }"
      class="advertisement"
      :style="adStyles[index]"
      ref="advertisements"
    >
      <img :src="adv.src" :alt="'Advertisement ' + (index + 1)">
    </div>
  </div>
</template>

<script>
export default {
  name: "AdvertisementBlock",
  props: {
    advertisements: Array
  },
  data() {
    return {
      adTopOffsets: [],
      adContainerTop: 0,
      adContainerHeight: 0,
      lastScrollTop: 0,
      adStyles: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.adTopOffsets = this.getAdTopOffsets();
      this.adContainerTop = this.$refs.wrapper.offsetTop;
      this.adContainerHeight = this.$refs.wrapper.offsetHeight;
    });
    window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      const scrollTop = window.scrollY || window.pageYOffset;
      this.lastScrollTop = scrollTop;
      this.updateAdStyles();
    },
    getAdTopOffsets() {
      return this.$refs.advertisements ? this.$refs.advertisements.map(ref => ref.offsetTop) : [];
    },
    shouldFixAd(index) {
      if (this.$refs.advertisements && this.$refs.advertisements.length > 1) {
        const adv2Bottom = this.$refs.advertisements[1].getBoundingClientRect().bottom;
        return index === 2 && adv2Bottom <= 0;
      }
      return false;
    },
    updateAdStyles() {
      if (this.$refs.advertisements && this.$refs.advertisements.length > 2) {
        const adv2Bottom = this.$refs.advertisements[1].getBoundingClientRect().bottom;
        this.adStyles = this.adTopOffsets.map((_, index) => ({
          position: index === 2 && adv2Bottom <= 0 ? 'fixed' : 'static',
          top: index === 2 && adv2Bottom <= 0 ? '0' : 'auto',
          width: '100%',
          zIndex: index === 2 && adv2Bottom <= 0 ? 1000 : 'auto'
        }));
      }
    }
  }
};
</script>

<style scoped>
.advertisement-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  padding-bottom: 100px;
}

.advertisement {
  margin-bottom: 20px;
}

.fixed-advertisement {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  transition: top 0.3s ease;
}

.advertisement img {
  max-width: 100%;
}
</style>
