<template>
  <div class="content-area">
    <div class="filter-container">
      <!-- City Filter -->
      <div class="dropdown">
        <select id="city-select" v-model="localSelectedCity" @change="updateTownOptions">
          <option value="">請選擇縣市</option>
          <option v-for="city in cityOptions" :key="city" :value="city">{{ city }}</option>
        </select>
      </div>
      <!-- Town Filter -->
      <div class="dropdown">
        <select id="town-select" v-model="localSelectedTown">
          <option value="">請選擇鄉鎮市區</option>
          <option v-for="town in townOptions" :key="town" :value="town">{{ town }}</option>
        </select>
      </div>
    </div>

    <div class="table-container">
      <!-- Restaurant Table -->
      <table class="restaurant-table">
        <thead>
          <tr>
            <th>編號</th>
            <th>行政區域</th>
            <th>鄉鎮區</th>
            <th>商家</th>
            <th>地址</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in pagedData" :key="item.ID" :class="{ 'odd-row': index % 2 !== 0, 'hover-row': hoverIndex === index }" @mouseover="hoverIndex = index" @mouseleave="hoverIndex = null">
            <td>{{ getIdNumber(index) }}</td>
            <td>{{ item.City }}</td>
            <td>{{ item.Town }}</td>
            <td @click="openUrl(item.Url)" :style="{ color: item.Url ? 'rgb(12, 164, 211)' : 'black' }" class="restaurant-name">{{ item.Name }}</td>
            <td class="address-cell" :title="item.Address">{{ item.Address.length > 20 ? item.Address.substring(0, 20) + '...' : item.Address }}</td>
          </tr>
        </tbody>
      </table>

      <!-- Pagination -->
      <div class="pagination">
        <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
        <div class="page-buttons">
          <button v-for="pageNumber in pageNumbers" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">{{ pageNumber }}</button>
        </div>
      </div>
    </div>
  </div>
</template>




<script>
import axios from 'axios';

export default {
  name: 'TableMode',
  props: {
    selectedCity: String,
    selectedTown: String,
  },
  data() {
    return {
      allData: [],         // 所有數據
      currentPage: 1,      // 當前頁碼
      itemsPerPage: 10,    // 每頁顯示數量
      loading: false,      // 是否正在加載
      error: null,         // 錯誤信息
      cityOptions: [],     // 城市選項
      townOptions: [],     // 鄉鎮區選項
      localSelectedCity: '',    // 當前選擇的城市（本地副本）
      localSelectedTown: '',    // 當前選擇的鄉鎮區（本地副本）
      hoverIndex: null     // 鼠標懸停的行索引
    };
  },
  computed: {
    filteredData() {
      const city = this.localSelectedCity;
      const town = this.localSelectedTown;

      // 如果選擇了縣市但未選擇鄉鎮市區，顯示選擇的縣市的所有資料
      if (city && !town) {
        return this.allData.filter(item => item.City === city);
      }
      
      // 如果選擇了鄉鎮市區，過濾出符合縣市和鄉鎮市區的資料
      if (town) {
        return this.allData.filter(item =>
          (!city || item.City === city) &&
          item.Town === town
        );
      }
      
      // 如果未選擇縣市和鄉鎮市區，顯示所有資料
      return this.allData;
    },
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredData.slice(start, start + this.itemsPerPage);
    },
    pageNumbers() {
      return Array.from({ length: this.totalPages }, (_, i) => i + 1);
    },
  },
  created() {
    if (!this.selectedCity) {
      this.localSelectedCity = ''; // 預設不選擇任何城市
    }
    if (!this.selectedTown) {
      this.localSelectedTown = ''; // 預設不選擇任何鄉鎮市區
    }
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx');
        this.allData = response.data;
        this.updateCityOptions();
      } catch (error) {
        this.error = '資料取得錯誤，請稍後重試。';
      } finally {
        this.loading = false;
      }
    },
    updateCityOptions() {
      this.cityOptions = [...new Set(this.allData.map(item => item.City))].sort();
      this.updateTownOptions();
    },
    updateTownOptions() {
      if (this.localSelectedCity) {
        this.townOptions = [...new Set(this.allData.filter(item => item.City === this.localSelectedCity).map(item => item.Town))].sort();
        this.localSelectedTown = '';  // 清空選擇的鄉鎮市區
      } else {
        this.townOptions = [];
        this.localSelectedTown = '';  // 清空選擇的鄉鎮市區
      }
    },
    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    openUrl(url) {
      if (url) {
        window.open(url, '_blank');
      }
    },
    getIdNumber(index) {
      return (this.currentPage - 1) * this.itemsPerPage + index + 1;
    },
  },
};
</script>



<style scoped>
.content-area {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 靠左对齐 */
  padding: 20px;
  margin: 0 auto;
  max-width: 1200px; /* 最大宽度 */
}

.filter-container {
  display: flex;
  flex-direction: row; /* 默认水平排列 */
  gap: 20px;
  margin-bottom: 20px;
  width: 100%;
}

/* 下拉選單容器樣式 */
.dropdown {
  display: flex;
  align-items: center;
}

/* 下拉選單樣式 */
.dropdown select {
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  background-color: #fff;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

/* 下拉選單聚焦樣式 */
.dropdown select:focus {
  outline: none;
  border-color: rgb(12, 164, 211);
  background-color: #f9f9f9;
}

/* 下拉選單的選項樣式 */
.dropdown option {
  padding: 8px 12px;
}

/* 下拉選單的選擇高亮 */
.dropdown select:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

.table-container {
  width: 100%;
  margin-top: 20px;
}

.restaurant-table {
  width: 100%;
  border-collapse: collapse;
  font-family: Arial, sans-serif;
}

/* 表头样式 */
.restaurant-table th {
  background-color: rgb(12, 164, 211); /* 表头背景颜色 */
  color: white; /* 表头文字颜色 */
  font-weight: bold;
  padding: 12px;
  text-align: left;
}

/* 奇偶行背景颜色 */
.restaurant-table tr:nth-child(odd) {
  background-color: #f9f9f9; /* 白色 */
}

.restaurant-table tr:nth-child(even) {
  background-color: #e0e0e0; /* 灰色 */
}

/* 鼠标悬停行背景颜色 */
.restaurant-table tr:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

/* 表格单元格样式 */
.restaurant-table th, .restaurant-table td {
  border: 1px solid #ddd;
  padding: 12px;
}

/* 地址单元格样式 */
.address-cell {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; /* 地址内容过长时显示省略号 */
  position: relative; /* 为伪元素定位 */
}

/* 鼠标悬停地址单元格效果 */
.address-cell:hover {
  background-color: #f0f0f0; /* 鼠标悬停时的背景颜色 */
}

.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.page-info {
  margin-bottom: 10px;
}

.page-buttons {
  display: flex;
  gap: 5px;
}

.page-button {
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.page-button.active, .page-button:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

/* 手機模式下的篩選器布局 */
@media (max-width: 768px) {
  .filter-container {
    flex-direction: column;
    align-items: stretch; /* 下拉選單佔滿寬度 */
  }

  .dropdown {
    width: 100%; /* 讓下拉選單在手機上佔滿整行 */
  }

  .dropdown select {
    width: 100%;
  }
}

</style>