<template>
  <div class="content-area">
    <!-- 篩選控制 -->
    <div class="filter-container">
      <!-- 縣市篩選 -->
      <div class="filter-dropdown">
        <select v-model="localSelectedCity" @change="updateTownOptions" aria-label="選擇縣市">
          <option value="" disabled>請選擇縣市</option>
          <option v-for="city in cityOptions" :key="city" :value="city">{{ city }}</option>
        </select>
      </div>
      <!-- 鄉鎮篩選 -->
      <div class="filter-dropdown">
        <select v-model="localSelectedTown" aria-label="選擇鄉鎮市區">
          <option value="" disabled>請選擇鄉鎮市區</option>
          <option v-for="town in townOptions" :key="town" :value="town">{{ town }}</option>
        </select>
      </div>
    </div>

    <!-- 餐廳卡片 -->
    <div class="card-container">
      <div v-for="item in pagedData" :key="item.ID" class="restaurant-card" @mouseenter="handleMouseEnter(item.ID)" @mouseleave="handleMouseLeave(item.ID)">
        <div class="restaurant-image">
          <img :src="item.PicURL" alt="餐廳圖片">
          <div class="overlay"></div>
        </div>
        <div class="restaurant-info">
          <div class="location-info">
            <div class="city-label">
              {{ item.City }}
            </div>
            <div class="town-label">
              {{ item.Town }}
            </div>
          </div>
          <div class="restaurant-name" :style="{ color: item.Url ? 'rgb(12, 164, 211)' : 'white' }" @click="openUrl(item.Url)">
            {{ item.Name }}
          </div>
          <div class="separator"></div>
          <div class="description-container">
            <div class="line-separator" v-show="visibleDescriptions.has(item.ID)"></div>
            <div class="description-text" v-show="visibleDescriptions.has(item.ID)">
              {{ item.FoodFeature }}
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 分頁 -->
    <div class="pagination">
      <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
      <div class="page-buttons">
        <button v-for="pageNumber in pageNumbers" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">
          {{ pageNumber }}
        </button>
      </div>
    </div>

    <!-- 錯誤訊息 -->
    <div v-if="error" class="error-message">{{ error }}</div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'CardMode',
  props: {
    advertisements: Array,
    selectedCity: String,
    selectedTown: String,
  },
  data() {
    return {
      allData: [],
      currentPage: 1,
      itemsPerPage: 10,
      loading: false,
      error: null,
      cityOptions: [],
      townOptions: [],
      localSelectedCity: '',
      localSelectedTown: '',
      visibleDescriptions: new Set(),
      isFiltering: false,
      isMobileMode: false,
      isAdHidden: false,
    };
  },
  watch: {
    localSelectedCity() {
      this.isFiltering = true;
      this.updateTownOptions();
    },
    localSelectedTown() {
      this.isFiltering = true;
    },
  },
  computed: {
    filteredData() {
      const city = this.localSelectedCity;
      const town = this.localSelectedTown;
      return this.allData.filter(item =>
        (!city || item.City === city) &&
        (!town || item.Town === town)
      );
    },
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      return this.filteredData.slice(start, start + this.itemsPerPage);
    },
    pageNumbers() {
      return Array.from({ length: this.totalPages }, (_, i) => i + 1);
    },
  },
  methods: {
    truncateText(text, length) {
      if (!text) return '';
      return text.length > length ? text.slice(0, length) + '...' : text;
    },
    handleMouseEnter(itemID) {
      this.visibleDescriptions.add(itemID);
    },
    handleMouseLeave(itemID) {
      this.visibleDescriptions.delete(itemID);
    },
    openUrl(url) {
      if (url) {
        window.open(url, '_blank');
      }
    },
    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get('https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx');
        this.allData = response.data;
        this.updateCityOptions();
      } catch (error) {
        this.error = '資料取得錯誤，請稍後重試。';
      } finally {
        this.loading = false;
      }
    },
    updateCityOptions() {
      this.cityOptions = [...new Set(this.allData.map(item => item.City))].sort();
      this.updateTownOptions();
    },
    updateTownOptions() {
      if (this.localSelectedCity) {
        this.townOptions = [...new Set(this.allData.filter(item => item.City === this.localSelectedCity).map(item => item.Town))].sort();
        this.localSelectedTown = '';
      } else {
        this.townOptions = [];
        this.localSelectedTown = '';
      }
    },
    checkInitialDeviceMode() {
      const isMobile = window.innerWidth <= 1000;
      this.isMobileMode = isMobile;
      this.isAdHidden = localStorage.getItem('adHidden') === 'true';
      if (isMobile) {
        localStorage.setItem('adHidden', 'true');
      } else {
        localStorage.removeItem('adHidden');
      }
    }
  },
  created() {
    this.checkInitialDeviceMode();
    if (!this.selectedCity) this.localSelectedCity = '';
    if (!this.selectedTown) this.localSelectedTown = '';
    this.fetchData();
  },
  mounted() {
    if (localStorage.getItem('adHidden') === 'true') {
      this.isAdHidden = true;
    }
  }
};
</script>



<style lang="scss" scoped>
.content-area {
  display: flex;
  flex-direction: column;
  align-items: center; /* 確保內容區域居中 */
}

.filter-container {
  display: flex;
  flex-wrap: wrap; /* 手機版兩行顯示選單 */
  gap: 10px; /* 調整下拉選單間距 */
  margin-bottom: 30px;
  width: 100%; /* 選單容器佔滿寬度 */
}

/* 桌面版 */
@media (min-width: 1001px) {
  .filter-container {
    flex-direction: row; /* 水平顯示選單 */
    justify-content: flex-start; /* 選單靠左對齊 */
  }
  
  .filter-dropdown {
    width: auto; /* 桌面版選單寬度自適應 */
  }
}

/* 手機版 */
@media (max-width: 768px) {
  .filter-container {
    flex-direction: column; /* 手機版垂直顯示選單 */
    align-items: stretch; /* 使選單佔滿整個寬度 */
  }
  
  .filter-dropdown {
    width: 100%; /* 手機版選單佔滿最大寬度 */
  }
}

.filter-dropdown select {
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  background-color: #fff;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s ease, border-color 0.3s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* 輕微陰影 */
}

/* 下拉選單聚焦樣式 */
.filter-dropdown select:focus {
  outline: none;
  border-color: rgb(12, 164, 211);
  background-color: #f9f9f9;
}

/* 下拉選單的選項樣式 */
.filter-dropdown option {
  padding: 8px 12px;
}

/* 下拉選單的選擇高亮 */
.filter-dropdown select:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); /* 自適應卡片寬度 */
  gap: 20px;
  width: 100%;
  margin-top: 20px;
}

.restaurant-card {
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  overflow: hidden;
  transition: box-shadow 0.3s ease;
  cursor: pointer;
  height: auto; /* 自適應高度 */
  max-width: 100%;
}

.restaurant-image {
  width: 100%;
  height: 200px; /* 設置圖片高度 */
  overflow: hidden;
  position: relative;
}

.restaurant-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.restaurant-image:hover img {
  transform: scale(1.3);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s ease;
  opacity: 0;
}

.restaurant-card:hover .overlay {
  opacity: 1;
}

.restaurant-info {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 8px;
  color: white;
  text-align: left;
  transition: opacity 0.3s ease;
  opacity: 1; /* 默認顯示 */
}

.description-container {
  position: relative;
}

.line-separator {
  display: block;
  width: 3em; /* 自適應寬度 */
  height: 4px; /* 調整線條高度 */
  background-color: #fff; /* 調整線條顏色 */
  margin-bottom: 8px; /* 調整上下間距 */
}

.description-text {
  font-size: 14px;
  line-height: 1.4em;
  margin-top: 8px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 2; /* 限制顯示為兩行 */
  text-overflow: ellipsis; /* 超出部分顯示省略號 */
}

.restaurant-card:hover .line-separator {
  display: block;
}

.restaurant-card:hover .description-text {
  opacity: 1; /* 鼠標懸停時顯示 */
}

.restaurant-name, .city-label, .town-label {
  position: relative;
}

.restaurant-name {
  font-weight: bold;
  font-size: 16px; /* 調整餐廳名稱大小 */
  color: white;
  cursor: pointer;
  transition: color 0.3s ease;
}

.restaurant-name:hover {
  color: rgb(12, 164, 211);
}

.location-info {
  display: flex;
  gap: 8px;
  margin-bottom: 5px;
}

.city-label {
  background-color: rgb(12, 164, 211);
  color: white;
  padding: 6px 12px; /* 調整內邊距 */
  border-radius: 4px;
  font-size: 16px; /* 調整文字大小 */
}

.town-label {
  background-color: rgba(102, 102, 102, 0); /* 半透明深灰色 */
  color: white;
  padding: 6px 12px; /* 調整內邊距 */
  border-radius: 4px;
  font-size: 14px; /* 調整文字大小 */
  font-style: italic; /* 文字斜體 */
}

.pagination {
  display: flex;
  flex-direction: column; /* 在小螢幕上垂直顯示頁碼 */
  align-items: center;
  gap: 15px;
  margin-top: 20px;
}

.page-info {
  margin-bottom: 10px;
  font-size: 16px;
}

.page-buttons {
  display: flex;
  flex-wrap: wrap; /* 使按鈕在空間不足時換行 */
  gap: 10px;
}

.page-button {
  padding: 6px 12px; /* 調整按鈕內邊距 */
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white;
  color: rgb(12, 164, 211);
  cursor: pointer;
  font-size: 14px; /* 調整按鈕文字大小 */
  transition: background-color 0.3s ease, color 0.3s ease;
}

.page-button.active {
  background-color: rgb(12, 164, 211);
  color: white;
}

.page-button:hover {
  background-color: rgb(12, 164, 211);
  color: white;
}

.error-message {
  color: red;
  font-size: 16px;
  margin-top: 20px;
}

@media (max-width: 1000px) {
  .advertisement-container {
    display: none; /* 隱藏廣告 */
  }
}

</style>